import { makeAutoObservable } from "mobx";
import axios from "axios";

class SeminarStore {
    seminars = []; //    
    loading = false; //  

    constructor() {
        makeAutoObservable(this);//         ..          (SeminarList)
        this.fetchSeminars();//     
    }

    //  
    async fetchSeminars() {
        this.loading = true;//   
        try {
            const response = await axios.get("http://localhost:5000/seminars"); //      
            this.seminars = response.data;//      
        } catch (error) {
            console.error("   :", error);//      
        } finally {
            this.loading = false;//   /    
        }
    }

    //   
    async deleteSeminar(id) {
        await axios.delete(`http://localhost:5000/seminars/${id}`);//    
        this.seminars = this.seminars.filter(seminar => seminar.id !== id);//        
    }

    //  
    async editSeminar(updatedSeminar) {
        try {
            await axios.put(`http://localhost:5000/seminars/${updatedSeminar.id}`, updatedSeminar); //    
            const index = this.seminars.findIndex(seminar => seminar.id === updatedSeminar.id); //     
            if (index !== -1) {
                this.seminars[index] = updatedSeminar; //    
            }
        } catch (error) {
            console.error("   :", error);
        }
    }
}

const seminarStore = new SeminarStore();// 
export default seminarStore;//    
